@media only screen and (max-width: 1376px){ 
    body{
        .main_2{
            .main_2_darkBlue{
                .main_2_darkBlue_container{
                    justify-content: space-around;
                    .main_2_darkBlue_item1{
                        margin-left: 0;
                    }
                    .main_2_darkBlue_item2{
                        padding-left: 0px;
                    }
                }
            }
        }
    }
  }
  
  @media screen and (max-width: 1022px){
    body{
        .main_2_darkBlue{
            .main_2_darkBlue_container{
                .main_2_darkBlue_item1{
                    text-align: center;
                }
                .main_2_darkBlue_item2{
                    padding-left: 0px;
                }
            }
        }
    }
    footer{
        .footer_container{
                .text_footer_item3{
                    text-align: center;
                    margin-top: -47px;
                }
        }
    }
  }
  
  @media screen and (max-width: 690px){
    body{
        header{
            .nav_menu_header{
                height: 80px;
                background-position: 100%;
                img{
                    margin-left: 16px;
                    margin-top: 13px;
                    width: 47px;
                }
                .nav_menu_header_container{
                    margin-right: 33px;
                    margin-top: 28px;
                    a{
                        font-size: 1em;
                        margin-right: 0px;
                    }
                    .acc{
                        color: $lightBlueText;
                        padding-right: 15px;
                    }
                    .cons{
                        padding-right: 15px;
                    }
                    #nav_menu_header_bttn{
                        font-size: 1em; 
                        padding: 1px 5px 4px 9px;
                        color: white;
                        border-color:$lightBlueText ;
                        img{
                            width: 15px;
                            height: 15px;
                            margin-left: 9px;
                            margin-top: 3px;
                        }
                    }
                }
            }
            .info_header{
                height: 185px;
                h5{
                    color: $lightBlueText;
                }
                p{
                    color: black;
                    font-size: 0.85em;
                    line-height: 24px;
                    margin-left: 30px;
                    margin-right: 30px;
                }
            }
        }
        .headertest{
            .test_header{
                .test_img_header{
                    height: 297px;
                    width: 395px;
                }
                .test_text_header{
                    .test_text_1_header{
                        font-size: 1.125em;
                    }
                    .test_text_2_header{
                        font-size: 0.875em;
                        line-height: 25px;
                        font-weight: 600;
                    }
                    #test_text_button_header{
                        font-size: $px27;
                        background-image:url("../../Design/Assets/png/background-test-buttn.png");
                    }
                }
            }
        }
        .main_1{
            .main_1_title{
                font-size: 1.6em;
            }
            .main_1_container_1{
                flex-wrap: nowrap;
                margin-left: 0;
                .main_1_item1{
                    padding-right: 131px;
                    img{
                        width: 34px;
                    }
                    p{
                        font-size: 1.125em;
                        margin-top: 19px;
                    }
                }
                .main_1_item2{
                    padding-left: 0px;
                    margin-right: -25px;
                    img{
                        width: 58px;
                    }
                    p{
                        font-size: 1.125em;
                    }
                }
            }
            .main_1_container_2{
                flex-wrap: nowrap;
                margin-left: 0;
                .main_1_item3{
                    padding-right: 131px;
                    padding-top: 19px;
                    img{
                        width: 92px;
                    }
                    p{
                        font-size: 1.125em;
                        margin-top: 19px;
                    }
                }
                .main_1_item4{
                    padding-left: 0px;
                    margin-right: 4px;
                    img{
                        width: 87px;
                    }
                    p{
                        font-size: 1.125em;
                    }
                }
            }
        }
        .main_2{
            margin-top: 71px;
            .main_2_darkBlue{
                .main_2_darkBlue_title{
                    font-size: 1.25em;
                }
                .main_2_darkBlue_container{
                    .main_2_darkBlue_item1{
                        line-height: 31px;
                        .main_2_darkBlue_item1_text1{
                            font-size: 1.1em;
                            color: white;
                        }
                        .main_2_darkBlue_item1_text2{
                            font-size: 1em;
                        }
                    }
                    .main_2_darkBlue_item2{
                        .main_2_darkBlue_contact_text{
                            font-size: 1.2em;
                        }
                        .main_2_darkBlue_contact_phone{
                            font-size: 1.5em;
                            height: 66px;
                            width: 179px;
                        }
                        .main_2_darkBlue_contact_mail{
                            font-size: 1.3em;
                        }
                    }
                }
            }
            .main_2_lightBlue_container{
                height: 214px;
                .main_2_lightBlue_text{
                    font-size: 1em;
                    padding-top: 23px;
                }
                .main_2_lightBlue_img{
                    margin-top: 29px;
                    width: 46px;
                }
                .main_2_lightBlue_buttn{
                    font-size: 1.2em;
                    background-image:url("../../Design/Assets/png/background-test-buttn.png");
                    width: 56%;
                    height: 45px;
                }
            }
        }
        footer{
            .footer_container{
                margin-top: 16px;
                .nav_menu_footer_item1{
                    margin-left: 12px;
                    margin-top: 8px;
                    #nav_menu_footer_bttn{
                        font-size: 1em;
                        padding: 1px 5px 4px 9px;
                        border-color:$lightBlueText ;
                        img{
                            width: 15px;
                            height: 15px;
                            margin-left: 9px;
                            margin-top: 3px;
  
                        }
                    }
                    .nav_menu_footer_link_blue{
                        font-size: 1em ;
                    }
                    .nav_menu_footer_link_black{
                        font-size: 1em;
                    }
                }
                .contact_footer_item2{
                    line-height: 40px;
                    .contact_footer_title{
                        font-size: 1.2em;
                    }
                    .contact_footer_text{
                        font-size: 0.8em;
                    }
                    .contact_footer_num{
                        font-size: 1em;
                    }
                }
                .text_footer_item3{
                    p{
                        font-size: 0.7em;
                    }
                    
                }
            }
        }
    }
  }

  @media only screen and (max-width: 1070px){
    body{
      main{
        .main_advice{
          .main_advice_title{
            font-size: $px18;
          }
          .main_advice_container{
            margin-left: 0;
            margin-right: 0;
            .main_advice_item{
              width: 50%;
              .main_advice_img--marg{
                width: 94px;
              }
              img{
                width: 70px;
              }
              p{
                font-size: 0.8125em;
                color: black;
              }
            }
            .main_advice_item__caugh{
              img{
                width: 87px;
                margin-right: 35px;
              }
            }
            .main_advice_item__house{
              width: 100%;
            } 
          }
        }
  
        .main_info{
          .main_info_text{
            line-height: 26px;
            .main_info_title{
              font-size: $px18;
            }
            .main_info_p{
              font-size: 0.8125em;
              padding-top: 13px;
            }
          }
          .main_info_link{
            a{
              font-size: 0.8125em;
            }
          }
          .main_info_link1{
            a{
              padding: 10px 15px;
            }
          }
          .main_info_link2{
            a{
              padding: 10px 42px;
            }
          }
        }
        .main_symp{
          .main_symp_title{
            font-size: 1.25em;
            margin: 80px 90px auto 90px;
          }
          .main_symp2_container{
            margin-left: 0;
            margin-right: 0;
            margin-top: 33px;
            .main_symptome{
              img{
                width: 66px;
              }
              .main_symptome--parag{
                font-size: 0.8125em;
                line-height: 25px;
              }
            }
          }
        }
      }
    }
  
  }